/* Home.css */

.home-container {
    background-color: black;
    color: red;
    padding: 20px;
    text-align: center;
  }
  
  .welcome-text {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .description-text {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .snake-game-button {
    background-color: red;
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .snake-game-button:hover {
    background-color: darkred;
  }
  